$color-white: #f3f6f6;
$color-wet-asphalt: #34495e;
$color-midnight-blue: #2c3e50;
$color-turquoise: #1abc9c;

/*reset external css*/
.toggle-menu {
	font-size: 1rem;
	top: calc(50% - 25px);
	left: calc(50% - 25px);

	&.is-open .burger-lines {
		background: transparent;

		&::before,
		&::after {
			background-color: $color-white;
		}
	}
}

.burger-lines {

}
.menu-list {
	padding: 0px;
	//width: 10em;
  height: 10em;
	margin: -5em;
/* generic styles for button & circular menu */
}
.pos-absolute {
	position: absolute;
	top: 145%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-size: 2rem;
}

/* generic link styles */
.nav-toggle,
.link {
	display: block;
	color: $color-white;
	text-align: center;
	text-decoration: none;
	text-shadow: 0 -1px 2px rgba(0,0,0,0.2);

	&:hover {
		opacity: 0.9;
	}
}

.nav-toggle {
	width: 3.25em;
	height: 3.25em;
	border-radius: 50%;
	z-index: 99;
}

