/* make the menu appear on click */
.nav-toggle + .menu-list.is-open {
	transform: scale(1) rotate(0deg);
	opacity: 1;
}

/* menu-list__item of the circular menu */
.menu-list__item {
	position: absolute;
	width: 50%;
	height: 50%;
	transform-origin: 100% 100%;
	background-color: transparent !important;
	overflow: hidden;

	/* covers for the inner part of the links so there's no hover trigger between
   star button & menu links; give them a red background to see them */
	&::after {
		content: "";
		position: absolute;
		top: 34%;
		left: 34%;
		width: 126%;
		height: 126%;
		border-radius: 50%;
		/* "unskew" = skew by minus the same angle by which parent was skewed */
		transform: skewY(-60deg);
		background-color: rgba(0,0,0,0.01);
	}

	&:first-child {
		transform: rotate(-15deg) skewY(60deg);
	}

	&:nth-child(2) {
		transform: rotate(15deg) skewY(60deg);

		.link::after {
			opacity: 1;
		}
	}

	&:last-child {
		transform: rotate(45deg) skewY(60deg);
	}
}

/* menu links */
.link {
	width: 200%;
	height: 200%;
	border-radius: 50%;
	/* "unskew" & rotate by -A/2 */
	transform: skewY(-60deg) rotate(-15deg);
	background: linear-gradient(75deg, transparent 50%, #e91e63 50%, transparent 50%) no-repeat 36.5% 0,
		linear-gradient(-75deg, transparent 50%, #e91e63 50%, transparent 50%) no-repeat 63.5% 0,
		radial-gradient(rgba(255, 255, 255, 0) 51%, rgba(0, 0, 0, 0.87) 52%)
		;
	background-size: 15% 15%, 15% 15%, cover;
	line-height: 1.4;
	color: $color-wet-asphalt;

	/* arrow for middle link */
	&::after {
		content: "";
		position: absolute;
		top: calc(13% - 0.25em);
		left: calc(50% - 0.25em);
		width: 0.5em;
		height: 0.5em;
		background: linear-gradient(-45deg, $color-midnight-blue 50%, transparent 50%);
		transform: rotate(45deg);
		opacity: 0;
	}

	&:hover {
		color: $color-white;
	}

	&.is-active {
		color: $color-white;
		opacity: 1;
	}
}


