.chat-txt {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  word-break: break-word;
  font-size: 12px;
  max-height: 9em;
  margin: 0 12px;
}

.chat-txt-limit {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  word-break: break-word;
  font-size: 12px;
  /* margin-right: 5px; */
  text-overflow: ellipsis;
}

.markdown-size * {
  font-size: 14px;
  margin: 0;
}

.chat-post-limit {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  font-size: 14px;
  word-break: break-word;
}

.chat-drop-limit {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  max-height: 20px;
  font-size: 14px;
  padding: 0 12px;
  font-family: inherit;
  white-space: pre-line;
  font-style: normal;
  margin-top: 0;

}

.chat-drop-unlimit {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  font-size: 14px;
  padding: 0 12px;
  min-height: 20px;
  font-family: inherit;
  font-style: normal;
  white-space: pre-line;
  margin-top: 0;
  word-break: break-word;
}

.chat-share-limit {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  font-size: 14px;
  padding: 0 12px;
  font-family: inherit;
  font-style: normal;
  margin-bottom: 5px;
}

