.social-card {
  float: left;
}
/* Component Styling */
.social-card {
  display: inline-block;
  border-radius: 6px;
  background-color: rgb(255, 255, 255);
  // box-shadow: 0px 4px 10px 0px rgba(34, 34, 34, 0.32);
  color: #444;
  width: 80%;
  user-select: none;
  text-align: left;
  position: relative;
}

.members {
  position: absolute;
  right: 1em;
}

.members__avatar {
  border-radius: 50%;
  width: 1.5em;
  height: 1.5em;
  border: 2px solid #fff;
  vertical-align: middle;
}

.members__avatar:nth-of-type(2) {
  width: 1.5em;
  height: 1.5em;
  margin: 0 -.5em;
  z-index: 2;
  position: relative;
}

.card__title {
  font-size: 1.2em;
  line-height: 1.2;
  margin: 0;
  color: rgba(0,0,0,.87);
  max-width: calc(80% - 4em);
}

.card__count {
  display: none;
}

.members__count {
  font-size: 0.8em;
  line-height: 1;
  margin-top: 0;
  color: rgba(0,0,0,.54);
  text-align: center;
}

.btn {
  border-radius: 4px;
  height: 36px;
  color: #fff;
  outline: none;
  border: 0;
  margin-top: 1.5em;
  text-transform: uppercase;
  width: 100%;
  font-size: 14px;
  cursor: pointer;
}

.btn--active {
  background-color: #eb1564 !important;
  color: #fff;
}

.btn--active:hover {
  background-color: #D3165F;
}

.btn--inactive {
  background-color: #00c853 !important;
  color: #ffffff;
}
// .btn--inactive:hover {
//   background-color: #e9ecef;
// }

.btn svg {
  vertical-align: bottom;
}
@media (max-width: 560px) {
  .social-card {
    cursor: pointer;
    max-width: 47.2%;
    text-align: center;
    margin-left: 8px;
    margin-bottom: 8px;
    z-index: 1;
    -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0, 0, 0, 0.24);
    -moz-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0, 0, 0, 0.24);
    float: left;
  }

  .members {
    position: static;
    padding-top: 10px;
  }

  .members__avatar {
    width: 2.5em;
    height: 2.5em;
  }

  .members__avatar:nth-of-type(2) {
    width: 3em;
    height: 3em;
    margin: 0 -1em;
  }

  .members__avatar.duo {
    width: 3em;
    height: 3em;
    margin: 0;
  }

  .card__title {
    font-size: 1em;
    margin-bottom: 0.5em;
    color: rgba(0,0,0,.87);
    max-width: none;
    height: 2.5em;
    text-align: -webkit-center;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    word-break: break-word;
  }

  .members__count {
    display: none;
  }

  .card__count {
    font-size: 0.8em;
    line-height: 1;
    margin-top: 0;
    color: rgba(0,0,0,.54);
    display: block;
  }
}

