.menu-toggler.checked ~ ul .menu-item {
  opacity: 1;
}
.menu-toggler.checked ~ ul .menu-item:nth-child(1) {
  transform: rotate(40deg) translateX(-110px);
}
.menu-toggler.checked ~ ul .menu-item:nth-child(2) {
  transform: rotate(90deg) translateX(-110px);
}
.menu-toggler.checked ~ ul .menu-item:nth-child(3) {
  transform: rotate(140deg) translateX(-110px);
}
.menu-toggler.checked ~ ul .menu-item a {
  pointer-events: auto;
}

.menu-item {
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 60px;
  height: 60px;
  display: block;
  opacity: 0;
  transition: 0.5s;
}
.menu-item a {
  display: table-cell;
  width: 60px;
  height: 60px;
  line-height: 60px;
  color: rgba(255, 255, 255, 0.7);
  background:rgba(206, 23, 30, 1);
  border-radius: 50px;
  text-align: center;
  vertical-align: middle;
  text-decoration: none;
  font-size: 40px;
  pointer-events: none;
  transition: 0.2s;
}
