.vjs-poster {
  background-size: contain;
}

.follow-icon {
  margin: auto;

  button {
    font-size: xx-small;
  }
}

.live-vote-icon {
  line-height: 0.5em;

  md-icon {
    color: white;
  }
}

.live-count {
  font-size: 13px;
}

.card-none-shadow {
  padding: 10px 0;
  box-shadow: none;
  border: 5px solid #191818;
}

.container-category {
  list-style: none;
  margin: 0;
  padding: 0;
}

.item-category {
  // background: tomato;
  padding: 5px;
  width: auto;
  height: 150px;
  margin: 5px;
  color: white;
  font-weight: bold;
  text-align: center;
  box-shadow: 0 14px 26px rgba(0, 0, 0, 0.04);
}
@media screen and (max-width: 480px) {
  .item-category {
    width: 44.99%;
  }
}
@media screen and (min-width:321px) and (max-width: 479px) {
  .item-category {
    width: 43.5%;
  }
}
/*float layout*/
.float {
  max-width: 1200px;
  margin: 0 auto;
}

.float:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.float-item {
  float: left;
}

.cat-icon {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border: 1px solid black;
  border-radius: 100%;
}

.cat-line-hight {
  line-height: 0.5em;

  p {
    line-height: 1.1em;
    font-family: inherit;
    font-style: normal;
    font-weight: 500;
    color: white;
  }
}

.live-head {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  object-fit: cover;
  padding: 2px;
}

.head-border {
  border: 1px solid #bdbdbd;
  padding: 5px;
}

.live-head-title {
  font-family: inherit;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  padding-left: 5px;
}

.mb-8 {
  margin-bottom: 8px;
}

.cat-grid-container {
  display: grid;
  grid-template-columns: 50% 50%;
}

.grid-category {
  padding: 5px;
  height: 160px;
  margin: 5px;
  color: white;
  font-weight: bold;
  text-align: center;
  // box-shadow: 0 14px 26px rgba(0, 0, 0, 0.04);
  box-shadow: 0 0 15px rgba(4, 4, 4, 0.7), 0 0 50px rgba(37, 5, 5, 0.8);
}
