.enter-grid-container {
  display: grid;
  grid-template-columns: 49% 49%;
  /* padding: 5px; */
  grid-column-gap: 8px;
  padding: 8px;
  grid-column-gap: 7px;
  grid-row-gap: 7px;
  background: black;
  min-height: 25em;
}

.black-frame {
  background: #0d0f0e;
  color: white;
}

.black-inner {
